.wrapper {
    position: fixed; 
    z-index: 2; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    background-color: rgb(0,0,0,0.4); 
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.content {
    background-color: #fefefe;
    margin: 100% auto; 
    padding: 20px;
    border: 1px solid #888;
    width: 80%; 
}
  
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
  
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.img {
    display: block;
}

.button {
    padding: 0;
    height: fit-content;
    width: fit-content;
}

.icons {
    display: flex;
    gap: 2%
}

.custom {
    display: flex;
}